:root {
  --GreyLighter: #f1f1f1;
  --GreyLight: #e7e7e7;
  --GreyDark: #6b6b6b;
  --Highlight: #fcaf09;
}

* {
  box-sizing: border-box;
  margin: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

h1 {
  text-transform: uppercase;
  color: var(--GreyDark);
  font-family: Trebuchet MS, Arial, sans-serif;
  font-weight: bolder;
}

#search-box {
  font: inherit;
  width: 550px;
  background-color: var(--GreyLighter);
  border-radius: 5px;
  outline: none;
  padding: 8px;
  display: inline-block;
}

#search-box::-ms-input-placeholder {
  text-transform: uppercase;
  opacity: .4;
}

#search-box::placeholder {
  text-transform: uppercase;
  opacity: .4;
}

#search-box:hover, #search-box:active, #search-box:focus {
  border: 2px solid var(--Highlight);
  background-color: var(--GreyLight);
}

.clearBtn {
  width: 40px;
  opacity: .5;
  cursor: pointer;
  margin-top: 2px;
  margin-left: -50px;
  transition: all .2s ease-in-out;
  position: absolute;
}

.clearBtn:hover {
  opacity: .9;
}

.country-info img {
  max-width: 80px;
  max-height: 50px;
  margin-right: 15px;
  box-shadow: 1px 4px 20px -9px rgba(0, 0, 0, .8);
}

.country-info p {
  line-height: 2;
}

.country-list {
  padding: 5px;
  list-style: none;
}

.country-list a {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.country-list li {
  width: 540px;
  background-color: var(--GreyLight);
  border-radius: 5px;
  outline: none;
  align-items: center;
  padding: 5px;
  transition: all .3s ease-in-out;
  display: flex;
}

.country-list a:nth-child(2n+1) li {
  background-color: var(--GreyLighter);
}

.country-list a li:hover {
  color: var(--Highlight);
  background-color: var(--GreyDark);
  font-weight: bold;
}

.country-list img {
  width: 50px;
  height: 35px;
  box-shadow: 1px 4px 17px -9px rgba(0, 0, 0, .8);
}

.country-list p {
  margin-left: 15px;
  font-size: 18px;
}

